body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  margin: 0 auto;
  padding: 32px 16px 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  width: 166px;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__reviews {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews--count {
  color: #060b35;
  align-items: center;
  height: 16px;
  margin: 0 0 0 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 16px;
  display: flex;
}

.card__price--name {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

.card__price--number {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__buy {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 0 0 16px;
  padding: 12px 70px 12px 69px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 4px;
}
